@import "../../assets/scss/app.scss";

.skills-overlay {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(13, 13, 13, 0.7);
}

.skills-modal {
  z-index: 999;
  position: absolute;
  border-radius: 20px;
  top: 50%;
  left: 20px;
  right: 20px;
  transform: translate(0, -50%);
  padding: 20px;
  //box-shadow: 0 0 10px 5px #141414;
  outline: none;
  background-color: rgba(24, 21, 21, 0.89);

  @include desktop() {
    left: 50%;
    right: auto;
    transform: translate(-50%, -50%);
    min-width: 800px;
  }

  &-title {
    position: relative;
    text-transform: uppercase;
    z-index: 2;
    font-family: Overlock;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.01em;
    color: #FAFAFA;

    @include desktop() {
      font-size: 48px;
    }

    & span {
      color: #CF5929;
    }
  }

  &-menu {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;

  &-item {
    cursor: pointer;
    text-align: center;
    font-family: Comfortaa;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: #FAFAFA;

    &_selected {
      color: #CF5929;
    }
  }

  }

  &-content {
    margin-top: 10px;
    border: 1px solid #262424;
    padding: 10px;
    display: flex;

    .skill-block {
      display: flex;

      &:not(:first-child) {
        margin-top: 20px;
      }

      .skills-modal-iconBlock {
        display: flex;
        flex-direction: column;
        //align-items: center;

        .skills-modal-iconWrapper {
          width: 100px;
          height: 100px;
          position: relative;

          p {
            position: absolute;
            z-index: 100;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-transform: uppercase;
            font-family: Overlock;
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 110%;
            letter-spacing: 0.01em;
            color: #EC832E;
            z-index: 101;
            text-align: center;
          }



          .skills-modal-iconWrapper-back {
            width: 100px;
            height: 100px;
          }

          .skills-modal-iconWrapper-icon {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 60%;
            transform: translate(-50%, -50%);
          }
        }

        &-name {
          margin-top: 10px;
          background: #494949;
          opacity: 0.8;
          border-radius: 5px;
          padding: 5px 0px;
          text-align: center;
          font-family: Comfortaa;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 16px;
          color: #FAFAFA;
          text-transform: uppercase;
        }

      }

      .skills-modal-description {
        align-self: center;
        margin-left: 20px;
      }
    }


  }

  &-close {
    z-index: 2;
    position: absolute;
    cursor: pointer;
    top: 25px;
    right: 25px;
    width: 17px;
    height: 17px;
  }

}

